class Search extends HTMLElement {
  connectedCallback() {
    this.render();
  }

  render() {
    this.innerHTML = `
            <div>
                <button class="bg-slate-500 bg-opacity-10 p-1">Search</button>
                <input type="text">
            </div>
        `;
  }
}

customElements.define('search-tag', Search);
